import { Component } from '@angular/core';

@Component({
    selector: 'nav-menu',
    template: require('./navmenu.component.html'),
    styles: [require('./navmenu.component.scss')]
})
export class NavMenuComponent {
    public menuShow: boolean = false;

    constructor(){};

    public toggleMenu(){
        this.menuShow = !this.menuShow;
    }
}



